.wrap.llms-reporting,
.wrap.lifterlms-settings {

	.llms-header {
		background: #fff;
		border-bottom: 1px solid #efefef;
		margin: -10px -20px 0;
		padding: 10px 0;
		z-index: 1;

		.lifterlms-logo {
			max-width: 140px;
		}

		.llms-save {
			float: right;
		}

		.llms-inside-wrap {
			padding: 0 10px;
		}

	}

	.llms-inside-wrap {
		box-sizing: border-box;
		max-width: 1000px;
		margin: 0 auto;
	}

	.llms-nav-tab-wrapper.llms-nav-secondary {
		background: #fff;
		box-shadow: 0 1px 3px rgba( 0, 0, 0, .13 );
		margin: 0 -20px 20px;
		z-index: 1;

		.llms-nav-item {
			.llms-nav-link:hover,
			&.llms-active .llms-nav-link {
				background: #fafafa;
				color: $color-blue;
				border-top-color: $color-blue;
			}

			&.llms-active .llms-nav-link {
				font-weight: 700;
			}
		}

		.llms-nav-link {
			border-top: 2px solid transparent;
			padding: 14px;
		}

	}

	.llms-setting-group {

		background: #fff;
		box-shadow: 0 1px 3px rgba( 0, 0, 0, .13 );
		margin: 0 0 20px;
		padding: 20px;

		.llms-label {
			border-bottom: 1px solid #efefef;
			font-weight: 700;
			font-size: 20px;
			padding: 20px;
			margin: -20px -20px 20px;
		}

		.llms-help-tooltip .dashicons {
			color: #444;
			cursor: help;
		}

		.form-table {
			margin: 0;
			tr:first-child .llms-subtitle {
				margin-top: 0;
			}
		}

		td[colspan="2"] {
			padding-top: 0;
			padding-left: 0;
		}

		tr.llms-disabled-field {
			opacity: 0.5;
			pointer-events: none;
		}

		input[type="text"],
		input[type="password"],
		input[type="datetime"],
		input[type="datetime-local"],
		input[type="date"],
		input[type="month"],
		input[type="time"],
		input[type="week"],
		input[type="number"],
		input[type="email"],
		input[type="url"],
		input[type="search"],
		input[type="tel"],
		input[type="color"],
		select,
		textarea, {
			width: 50%;
			&.medium { width: 30%; }
			&.small { width: 20%; }
			&.tiny { width: 10%; }
		}

	}

	@media only screen and (min-width: 782px) {
		.llms-nav-tab-wrapper.llms-nav-secondary {
			.llms-nav-item {
				.llms-nav-link:hover,
				&.llms-active .llms-nav-link {
					background: #fff;
				}
			}
		}
	}

	// Email Delivery providers.
	#llms-mailhawk-connect {
		font-size: 16px;
		height: auto;
		margin: 0 0 6px;
		padding: 8px 14px;
		position: relative;

		.dashicons {
			margin: -4px 4px 0 0;
			vertical-align: middle;
		}
	}
	#llms-sendwp-connect {
		font-size: 16px;
		height: auto;
		margin: 0 0 6px;
		padding: 8px 14px;
		position: relative;

		.fa {
			margin-right: 4px;
		}
	}

}

@media only screen and (min-width: 782px) {
	.wrap.lifterlms-settings {
		.llms-header {
			position: sticky;
			top: 30px;
		}
		.llms-nav-tab-wrapper.llms-nav-secondary {
			position: sticky;
			top: 93px;
		}
	}
}

.wrap.llms-reporting {
	.llms-inside-wrap {
		box-sizing: border-box;
		max-width: 1000px;
		margin: 0 0 0 20px;
	}
}
